@import './reset.scss';
$메인칼라 : #ff0000;


//css를 함수에 담아서 편리하게 사용가능
@mixin 함수() {
    background: #eee;
    padding: 20px;
    border-radius: 5px;
    max-width: 500px;
    width: 100%;
    margin: 0 auto;
}

.red {
    color: $메인칼라;
}

.my-alert {
    @include 함수()
    p {
        margin: 0;
    }
}

//@extend 해당 css를 복사해서 가져옴
.my-alert-yg {
    @extend .my-alert;
    background: yellowgreen;
}



